import React from 'react';
// import {AuthContext} from "../../../context";
import {BottomNavigation, BottomNavigationAction} from "@mui/material";
import HomeIcon from '@mui/icons-material/Home';
import AssignmentIcon from '@mui/icons-material/Assignment';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import {useNavigate} from "react-router-dom";

const MyNavbar = () => {
    // const {setIsAuth} = useContext(AuthContext);
    const [value, setValue] = React.useState(0);
    // const logout = () => {
    //     setIsAuth(false);
    //     localStorage.removeItem('auth')
    // }
    const router = useNavigate();

    return (
        <BottomNavigation
            showLabels
            value={value}
            onChange={(event, newValue) => {
                setValue(newValue);
            }}
            style={{
                width: '100%',
                position: 'fixed',
                bottom: 0,
                right: 0,
            }}
        >
            <BottomNavigationAction label="" icon={<HomeIcon />} onClick={() => router(`/about`)}/>
            <BottomNavigationAction label="" icon={<AssignmentIcon />}  onClick={() => router(`/posts`)}/>
            <BottomNavigationAction label="" icon={<AccountCircleIcon />} onClick={() => router(`/about`)}/>
        </BottomNavigation>
    );
};

export default MyNavbar;